<template>
  <el-row :gutter="20">
    <el-col :span="8" :offset="12">
      <div v-show="!ifTableEdit" class="tableEditWrapper">
        <el-button class="edit" icon="edit" @click.native="tableEdit()">编辑</el-button>
      </div>
      <div v-show="ifTableEdit" class="tableEditWrapper">
        <el-button class="cancle" icon="edit" @click.native="tableCancle()">退出编辑</el-button>
      </div>
    </el-col>
    <el-col :span="19" class="leftReset">
      <el-form ref="form" label-position="right"  label-width="120px">
        <el-row type="flex" align="middle">
          <el-col :span="14" :offset="2" v-show="ifTableEdit">
            <el-button-group style='float:right'>
              <el-button class="filter-item" @click="add()" type="primary" icon="edit">新建用户</el-button>
              <!-- <el-button class="filter-item" @click="handleCreate" type="primary" icon="edit">行内新建</el-button> -->
            </el-button-group>
          </el-col>
        </el-row>
        <table class="table">
          <thead class="thead">
          <tr>
            <th v-show="ifTableEdit">
              <!-- <input type="checkbox" v-model="checkAll" > -->
              <el-checkbox v-model="checkAll" @change="changeState()"></el-checkbox>
            </th>
            <th>用户ID</th>
            <th>用户名</th>
            <th>手机号码</th>
            <th>用户昵称</th>
            <th>部门</th>
            <th>职位</th>
            <th>角色列表</th>
            <th>用户说明</th>
            <th>状态</th>
            <th v-show="ifTableEdit">操作</th>
          </tr>
          </thead>

          <tbody class="tbody">
          <tr v-for="(item, $index) in userDataList">
            <td v-show="ifTableEdit">
              <!-- <input type="checkbox"> -->
              <el-checkbox v-model="item.checked" @change="singleSelect()"></el-checkbox>
            </td>
            <td>{{item.id}}</td>
            <td>{{item.username}}</td>
            <td>{{item.phone}}</td>
            <td>{{item.nickname}}</td>
            <td>{{item.department}}</td>
            <td>{{item.position}}</td>
            <td >
              <span v-for="ro in item.roles">{{ro.name}}  </span>
            </td>
            <!--<td ></td>-->
            <td>{{item.note}}</td>
            <td>{{item.status==false? '禁用':'激活'}}</td>
            <td class="modifyTrData"  v-show="ifTableEdit" @click='edit(item)'><el-button>修改</el-button></td>
          </tr>
          </tbody>
        </table>

        <el-row type="fix" align="middle" style="margin-top: 20px">
          <el-col :span="8">
            <el-button @click='deleteUser' v-show="ifTableEdit">删除</el-button>
          </el-col>
          <el-col :span="16" style="float:right;">
            <el-pagination
                    @size-change="sizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="totalCount">
            </el-pagination>
          </el-col>
        </el-row>

      </el-form>
    </el-col>

    <el-dialog title="添加用户" :visible.sync="dialogFormVisible" :before-close="cancle">
      <el-form :model="user">
        <el-form-item label="用户名" :label-width="formLabelWidth" v-if="user.id != null" prop="username">
          <el-input v-model="user.username" placeholder="用户名" auto-complete="off" :disabled="true" ></el-input>
        </el-form-item>
        <el-form-item label="用户名" :label-width="formLabelWidth" v-else>
          <el-input v-model="user.username" prop="username" auto-complete="off" placeholder="用户名" :disabled="false" ></el-input>
        </el-form-item>

        <el-form-item label="密码" :label-width="formLabelWidth"  v-if="user.id == null">
          <el-input v-model="user.password" placeholder="密码"  :label-width="formLabelWidth" :disabled="false" ></el-input>
        </el-form-item>
        <el-form-item label="手机号码" :label-width="formLabelWidth" prop="phone">
          <el-input v-model="user.phone" placeholder="手机号码" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="用户昵称" :label-width="formLabelWidth" prop="nickname">
          <el-input v-model="user.nickname" placeholder="用户昵称" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="部门" :label-width="formLabelWidth" prop="department">
          <el-input v-model="user.department" placeholder="部门" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="职位" :label-width="formLabelWidth" prop="position">
          <el-input v-model="user.position" placeholder="职位" auto-complete="off"></el-input>
        </el-form-item>

          <el-form-item label="角色列表" :label-width="formLabelWidth">
            <el-checkbox-group v-model="checkedroles">
              <el-checkbox v-for="role in roles" :label="role.id" :disabled="user.id == 1 && role.id==1?true:false" :key="role">{{role.name}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>

        <el-form-item label="用户说明" :label-width="formLabelWidth" prop="note">
          <el-input v-model="user.note" placeholder="用户说明" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="text" @click="resetPassword = true"
                   style="float:left;border-bottom:2px solid cornflowerblue;" v-if="user.id!=null">重置用户密码?
        </el-button>
        <el-button @click="cancle()">取 消</el-button>
        <el-button type="primary" @click="submit()">确 定</el-button>
      </div>
    </el-dialog>


    <el-col :span="12">
      <div class="grid-content bg-purple-dark" align="right">
        <el-dialog :visible.sync="resetPassword" size="tiny" top="30%" title="重置密码" ref="resetForm">
          <el-form :model="reset" :rules="resetRule" ref="reset">
            <el-form-item label="新密码" prop="pass">
              <el-input type="password" v-model="reset.pass" auto-complete="off" placeholder="请输入密码"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="checkPass">
              <el-input type="password" v-model="reset.checkPass" auto-complete="off"
                        placeholder="请再次输入密码"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="restPassCancle()">取 消</el-button>
            <el-button type="primary" @click="submitForm1('reset')">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </el-col>

  </el-row>
</template>
<script>
    import us from '@/api/user';
    import role from '@/api/roles';
//    import ElButton from "../../../../../node_modules/element-ui/packages/button/src/button";
    export default {
//        components: {ElButton},
        props: {
            type: {  //弹出框出现状态：新建：0，编辑：1
                type: Number,
                require: true,
            }
        },
        beforeCreate() {
            this.temp = {};
            this.tableDataTemp ={};
        },
        mounted() {
            this.getUserList();
        },
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.reset.checkPass !== '') {
                        this.$refs.reset.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.reset.pass) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                totalCount:0,
                currentPage:1,
                pageSize:10,
                pageSizes:[10,20,30,40],
                checkAll: false,
                dialogFormVisible: false,
                resetPassword: false,
                reset:{
                    pass: "",
                    checkPass: ""
                },
                resetRule: {
                    pass: [
                        {validator: validatePass, trigger: 'blur'}
                    ],
                    checkPass: [
                        {validator: validatePass2, trigger: 'blur'}
                    ]
                },
                visible2: false,
                ifEdit: false,
                ifTableEdit: false,
                dialogType:1,
                seachSel: '',
                seachSelect: '',
                userDataList: [],
                checkedroles:[],
                roles:[],
                ids:[],
                user: {
                    id:null,
                    username: null,
                    password: null,
                    phone: null,
                    nickname: null,
                    note: null,
                    status: null,
                    department: null,
                    position: null,
                    roles: null
                },
                checked: [],
                // 弹出框数据
                dialogFormData: {},
                listIndex: '',
                listQuery: {
                    page: 1,
                    limit: 10
                },
                currentPage: 1,
                defaultProps: {
                    children: 'children',
                    label: 'label'
                },
                formLabelWidth: '120px',
            };
        },
        methods: {
            add(){
                this.dialogFormVisible=true;
                this.getAppRoles();
            },
            edit(user) {
                this.dialogFormVisible=true;
                this.getAppRoles();
                if (user != null) {
                    this.user = user;
                    for (var i = 0; i < user.roles.length; i++) {
                        this.checkedroles.push(user.roles[i].id);
                    }
                }
            },
            deleteUser(){
                let that = this;
                var selectData = this.userDataList.filter(item => {
                    return item.checked
                });
                for(var i=0;i<selectData.length;i++) {
                    if(selectData[i].id==1){
                        that.getUserList();
                        that.$message({type: 'error', message: '超级管理员不能禁用'});
                    }else{
                        debugger
                        us.updateStatus(selectData[i].id).then(function (response) {
                            that.getUserList();
                            if (response.code == 0) {
                                that.$message({type: 'success', message: '用户状态更新成功'});
                                that.getUserList();
                            } else {
                                that.$message({type: 'error', message: '用户状态更新失败'});
                            }
                        });
                    }

                }
            },
            getAppRoles(){
                let that = this;
                role.getRoleList({
                    page: 1,
                    length: 1000,
                }).then(function (response) {
                    if (response.code == 0) {
                        that.roles = response.data.rows;
                    }
                });
            },
            handleCurrentChange (val) {
                this.currentPage = val;
                this.getUserList();
            },
            sizeChange(val){
                this.pageSize = val;
                this.getUserList();
            },
            getUserList(){
                setTimeout(() => {
                    let that = this;
                    us.getList({
                        page: that.currentPage,
                        length: that.pageSize
                    }).then(function (response) {
                        if (response.code == 0) {
                            that.userDataList = response.data.rows;
                            that.totalPage = response.totalPage;
                            that.totalCount = response.data.totalCount;
                        }
                        else that.$message({type: 'error', message: response.reason});
                    });
                }, 300);
            },
            submitForm1(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        var user = {
                            id: this.user.id,
                            password: this.reset.pass
                        };
                        let that = this;
                        us.resetPassword(user).then(function (response) {
                            if (response.code == 0) {
                                that.$message({type: 'success', message: "密码修改成功"});
                                that.restPassCancle();
                            } else {
                                that.$message({type: 'error', message: response.reason});
                            }
                        });
                    } else {
                        this.$message({type: 'error', message: "密码修改失败"});
                        return false;
                    }
                });
            },
            submit() {
                let that = this;
                that.user.roles = that.checkedroles;
                if(that.user.id==null) {
                    if ("" != that.user.username && null != that.user.username) {
                        us.getByName(that.user.username).then(function (response) {
                            if (response.code == 0) {
                                if (response.data != undefined && response.data.username != undefined) {
                                    that.$message({type: 'error', message: "创建用户失败!用户名已存在"});
                                } else {
                                    us.create(that.user).then(function (response) {
                                        if (response.code == 0) {
                                            that.$message({type: 'success', message: "创建用户成功"});
                                            that.cancle();
                                            that.getUserList();
                                        }
                                        else that.$message({type: 'error', message: "创建用户失败"});
                                        that.cancle();
                                        that.getUserList();
                                    });
                                }
                            }
                        });
                    } else {
                        that.$message({type: 'error', message: "创建用户失败!请输入用户名"});
                    }
                }else{
                    us.update(that.user).then(function (response) {
                        if (response.code == 0) {
                            that.$message({type: 'success', message: "编辑用户成功"});
                            that.cancle();
                            that.getUserList();
                        }
                        else that.$message({type: 'error', message: response.reason});
                    });
                }
            },
            restPassCancle(){
                let that = this;
                that.resetPassword = false;
//                that.reset={
//                    pass:"",
//                    checkPass:""
//                }
                that.reset.pass = "";
                that.reset.checkPass = "";

            },
            cancle() {
                this.ifEdit = false;
                this.dialogFormVisible = false;
                this.user = {
                    roles: [],
                    username: null,
                    password: null,
                    phone: null,
                    nickname: null,
                    department: null,
                    position: null,
                    note: null,
                };
                this.checkedroles = [];
            },
            confirm() {
                this.ifEdit = false;
            },
            tableEdit() {
                this.ifTableEdit = true;
            },
            tableCancle() {
                this.ifTableEdit = false;
            },
            handleRemove(file, fileList) {
            },
            // 行内创建表格
//            handleCreate(data) {
//                this.userDataList.splice(0, 0, data);
//            },
            // 全选、单选
            changeState(){
                let that = this;
                let checked = this.checkAll;
                this.userDataList.forEach(item => {
                    this.$set(item,"checked",checked)
                    item.checked = checked;
                    if(item.checked =checked){
                        that.ids.push(item.id);
                    }
                    console.log(item.checked+"*******"+item.id);
                })
            },
            singleSelect() {
                //console.log(this.ids.length);
//                var selectData = this.userDataList.filter(item => {
//                if(item.checked==true){
//                    console.log(item.id);
//                }
//                    if(item.checked == true){
//                        this.ids.push(item.id);
//                    }else{
//                        for(var i=0;i<this.ids.length;i++){
//                            if(this.ids[i]==item.id){
//                                this.ids.splice(this.ids[i]);
//                            }
//                        }
//                   }
//                    return item.checked == true
//                })
//                selectData.length == this.userDataList.length ? this.checkAll = true : this.checkAll = false;
            },


            },
            // 弹框取消按钮
            editCancle() {
                this.dialogFormData = this.tableDataTemp;
                console.log(this.dialogFormData,"this.dialogFormData");
                this.dialogFormVisible=false;
            },
            // 弹框确定按钮
            editDo(){
                // 请求后台
                this.dialogFormVisible=false;
            },
            // 分页
//            handleSizeChange(val) {
//                console.log(`每页 ${val} 条`);
//            },
//            handleCurrentChange(val) {
//                this.currentPage = val;
//                console.log(`当前页: ${val}`);
//            },

        filters: {
            formatDate(time) {
                var date = new Date(time);
                return parseTime(date, '{y}-{m}-{d}');
            }
        }
    };
</script>
<style lang="scss">
  @import "~styles/variable";

  .seachpre {
    width: 74px;
  }
  // button
  .editWrapper {
    float: right;
    .edit {
      width: 76px;
      padding: 7px 15px;
      color: $color-theme;
      border: 1px solid $color-theme;
    }
  }
  .filter-item {
    width: 100px;
    // width: 130px;
    height: 36px;
    margin-left: 20px;
    // color: $color-theme;
    border: 1px solid $color-theme;
    // background: none;
    border-radius: 4px !important;
  }

  .buttonWrapper {
    font-size: 12px;
    float: right;
    .confirm,
    .cancle {
      width: 76px;
      padding: 7px 15px;
    }
    .confirm {
      color: $color-theme;
      border: 1px solid $color-theme;
    }
    .cancle {
      color: $color-red;
      border: 1px solid $color-red;
    }
  }
  .tableEditWrapper {
    float: right;
    .edit {
      width: 102px;
      padding: 8px 15px;
      font-size: 13px;
      color: $color-theme;
      border: 1px solid $color-theme;
    }
    .cancle {
      width: 102px;
      padding: 8px 15px;
      font-size: 13px;
      color: $color-red;
      border: 1px solid $color-red;
    }
  }
  //  弹框
  .el-dialog__body .el-input,
  .el-dialog__body .el-date-editor {
    width: 360px !important;
  }
  // 分页
  .el-pagination {
    text-align: right;
    padding: 10px 0;
    float: right;
  }
  .el-pagination__total {
    float: left;
  }
  .upload-demo {
    float: left;
    .el-button {
      color: $color-theme;
      border-radius: 0px;
      background: none;
      border: 1px solid $color-theme;
    }
  }
  .loadTemplate {
    color: #2f3445;
    background: none;
    border: solid 1px #c0ccda !important;
  }
  .leftReset {
    padding-right: 20px !important;
  }
  .rightTitle {
    height: 50px;
    padding: 18px 0 18px 10px;
    line-height: 50px;
    box-sizing: border-box;
    background-color: #eff2f7;
    .form-group-title {
      margin: 0;
    }
  }
  // 表格
  .table {
    color: #2f3445;
    font-size: 13px;
    border: 1px solid #e0e6ed;
    .thead {
      background-color: #e5e9f2;
      th {
        width: 130px;
        height: 44px;
        text-align: center;
        border-right: 1px solid #e0e6ed;
      }
    }
    .tbody {
      tr {
        height: 46px;
        width: 130px;
        text-align: center;
        border-top: 1px solid #e0e6ed;
        td {
          border-right: 1px solid #e0e6ed;
        }
        .modifyTrData {
          color: #5c7dfe;
        }
      }
    }
  }
</style>



